import { Component, OnInit, AfterContentInit } from '@angular/core';
import { ActivatedRoute, Params, Router }   from '@angular/router';
import { Goods }        from '../../models/defineClass';
import { GoodsService } from '../../services/goods.service';
import { Location }     from '@angular/common';

@Component({
  selector: 'app-search-result',
  templateUrl: './search-result.component.html',
  styleUrls: ['./search-result.component.css']
})


export class SearchResultComponent implements AfterContentInit {

    public goodsArray: Array<any>;
    //private selectedTeacher: Goods;
    public selectedHero: Goods;

    // For : route.params.subscribe(params => {}
    private sub: any;
    private parameterText: string;   // Come from Url

    constructor(
                private goodsService: GoodsService,
                private location: Location,
                private route: ActivatedRoute
    ) {}

    // Angular  ngOnInit Angular 
    // Angular

    ngAfterContentInit() {

        this.sub = this.route.params.subscribe(params => {
            this.parameterText = params['search_Text']; // (+) converts string 'id' to a number

               //console.log("init : get Param , search_Text =   " + this.parameterText);

            // Just GeT all Smart Phones : cat_id = 1;  and their parent_id: 0
            this.goodsService
                .getGoods()
                .subscribe(goods => this.goodsArray = goods.filter(good => good.goods_name.toLowerCase().includes(this.parameterText.toLowerCase())));

        });
    }

    onSelect(hero: Goods): void {
        this.selectedHero = hero;
    }

    goBack(): void {
        this.location.back();
    }

    delete(goods: Goods): void {

        //console.log("Will delete id : " + goods.goods_id);

        let ts = [];
        let n  = 0;

        for(let i = 0; i<this.goodsArray.length; i++ ) {

            let t : Goods;

            t =  this.goodsArray[i];

            if (t.goods_id != goods.goods_id) {
                ts[n++] = t;
            }
        }

        this.goodsArray = ts;
    }
}
