import { NgModule }       from '@angular/core';
import { BrowserModule }  from '@angular/platform-browser';
import { FormsModule, ReactiveFormsModule }    from '@angular/forms';  // together for search Box
import { HttpClientModule }    from '@angular/common/http';
 
import { HttpClientInMemoryWebApiModule } from 'angular-in-memory-web-api';
import { InMemoryDataService }  from './services/in-memory-data.service';
 
import { AppRoutingModule }     from './app-routing.module';
 
import { AppComponent }         from './app.component';
import { DashboardComponent }   from './dashboard/dashboard.component';
import { HeroDetailComponent }  from './hero-detail/hero-detail.component';
import { HeroesComponent }      from './heroes/heroes.component';
import { HeroSearchComponent }  from './hero-search/hero-search.component';
import { MessagesComponent }    from './messages/messages.component';
import { TypePageComponent } from './views/type-page/type-page.component';
import { CartComponent } from './views/cart/cart.component';
import { OrderComponent } from './views/order/order.component';

import { HttpModule } from '@angular/http';
import { PayBillComponent } from './views/pay-bill/pay-bill.component';
import { DetailComponent } from './views/detail/detail.component';
import { BreadcrumbComponent } from './views/breadcrumb/breadcrumb.component';
import { SearchComponent } from './views/search/search.component';
import { SearchResultComponent } from './views/search-result/search-result.component';
import { SmallGoodsComponent } from './views/small-goods/small-goods.component';

@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    AppRoutingModule,
    HttpClientModule,
    HttpModule,
 
    // The HttpClientInMemoryWebApiModule module intercepts HTTP requests
    // and returns simulated server responses.
    // Remove it when a real server is ready to receive requests.
    HttpClientInMemoryWebApiModule.forRoot(
      InMemoryDataService, { dataEncapsulation: false }
    )
  ],
  declarations: [
    AppComponent,
    DashboardComponent,
    HeroesComponent,
    HeroDetailComponent,
    MessagesComponent,
    HeroSearchComponent,
    TypePageComponent,
    CartComponent,
    OrderComponent,
    PayBillComponent,
    DetailComponent,
    BreadcrumbComponent,
    SearchComponent,
    SearchResultComponent,
    SmallGoodsComponent
  ],
  bootstrap: [ AppComponent ]
})
export class AppModule { }